// export default Donations

'use client'

import { useEffect, useState } from "react"
import axios from "axios"
import { toast } from "sonner"

type Donation = {
  donorName: string
  item: string
  quantity: string
  location: string
}

type ApiResponse = {
  success: boolean
  message: string[]
  donations: Donation[]
}

const DonationsPage = () => {
  const [donations, setDonations] = useState<Donation[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchDonations = async () => {
      try {
        const response = await axios.get<ApiResponse>("http://localhost:9000/admin/donations", {
          withCredentials: true
        })

        if (response.data.success) {
          setDonations(response.data.donations)
        } else {
          toast.warning("Failed to fetch donations", {
            description: response.data.message.join(", ")
          })
        }
      } catch (error) {
        toast.error("Error fetching donations")
        console.error(error)
      } finally {
        setLoading(false)
      }
    }

    fetchDonations()
  }, [])

  return (
    <div className="p-6 min-h-screen bg-gray-100">
      <h1 className="text-3xl font-bold mb-6">Donation Records</h1>

      {loading ? (
        <p>Loading...</p>
      ) : donations.length === 0 ? (
        <p>No donations found.</p>
      ) : (
        <div className="grid gap-4">
          {donations.map((donation, idx) => (
            <div key={idx} className="bg-white rounded-md shadow-md p-4">
              <p><strong>Donor:</strong> {donation.donorName}</p>
              <p><strong>Item:</strong> {donation.item}</p>
              <p><strong>Quantity:</strong> {donation.quantity}</p>
              <p><strong>Location:</strong> {donation.location}</p>
            </div>
          ))}
        </div>
      )}
    </div>
  )
}

export default DonationsPage
